import "./App.css";
// Imports for React
import React, { useContext, useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  NavLink,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";
// Imports for Material UI
import "@fontsource/roboto";
import { createTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import a11yProps from "./shared/A11yProps";

import { AppBar, Tabs, Tab, Grid, Typography } from "@material-ui/core";

// Imports for Context
import { GarmsContext, UserContext, NeedsContext } from "./context";
// Imports for Components
import ProtectedRoute from "./shared/ProtectedRoute";
import Login from "./components/Login";
import Signup from "./components/Signup";
import AddGarm from "./components/AddGarmPage";
import OutfitBuilder from "./components/OutfitBuilderPage";
import Wardrobe from "./components/WardrobePage";
// import useFetch hook
import useFetch from "./hooks/useFetch";

const oliveAqua = createTheme({
  palette: {
    primary: {
      light: "#60ad5e",
      main: "#2e7d32",
      dark: "#005005",
      contrastText: "#fff",
    },
    secondary: {
      light: "#42a5f5",
      main: "#42a5f5",
      dark: "#0077c2",
      contrastText: "#000",
    },
  },
});
function App() {
  const { callAPI: getGarms } = useFetch("POST");
  // Local value state used for Tabs Navigation
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  // bringing in User context
  const { user, logout, user_id } = useContext(UserContext);
  const { garms, setGarms } = useContext(GarmsContext);
  const { setNeeds } = useContext(NeedsContext);

  useEffect(() => {
    if (user_id === null) {
      return;
    }
    async function call() {
      const res = await getGarms("/api/garms/user", { user_id });
      console.log("res is:", res);
      if (!res.success) {
        return console.error(res.error);
      }
      console.log("res.data is:", res.data);
      setGarms(res.data);
    }
    call();
  }, [user_id]);

  return (
    <ThemeProvider theme={oliveAqua}>
      <Router>
        <nav>
          <AppBar position="sticky" style={{ minHeight: "60px" }}>
            <Grid container spacing={0}>
              <Grid item xs={12} md={4} style={{ textAlign: "center" }}>
                <Typography
                  variant="h3"
                  component="h6"
                  color="inherit"
                  style={{ fontWeight: "bold" }}
                >
                  My Garms!
                </Typography>
              </Grid>
              {!user && (
                <Grid item xs={12} md={8}>
                  <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    variant="fullWidth"
                  >
                    <Tab
                      label="Login"
                      to="/login"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      {...a11yProps(0)}
                    />
                    <Tab
                      label="Signup"
                      to="/signup"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      {...a11yProps(1)}
                    />
                  </Tabs>
                </Grid>
              )}
              {user && (
                <Grid item xs={12} md={8}>
                  <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    variant="fullWidth"
                  >
                    <Tab
                      label="Add Garm"
                      to="/addGarm"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      {...a11yProps(0)}
                    />
                    <Tab
                      label="My Garms"
                      to="/myGarms"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      {...a11yProps(1)}
                    />
                    <Tab
                      label="Fits"
                      to="/fitBuilder"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      {...a11yProps(2)}
                    />
                    <Tab
                      label="Logout"
                      to="/login"
                      component={NavLink}
                      style={{ minHeight: "60px" }}
                      onClick={() => {
                        logout();
                        setGarms([]);
                        setNeeds([]);
                        setValue(0);
                      }}
                      {...a11yProps(3)}
                    />
                  </Tabs>
                </Grid>
              )}
            </Grid>
          </AppBar>
        </nav>
        <main>
          <Switch>
            <ProtectedRoute path="/login" reqUser={false} loggedInUser={user}>
              <Login setValue={setValue} />
            </ProtectedRoute>
            <ProtectedRoute path="/signup" reqUser={false}>
              <Signup setValue={setValue} />
            </ProtectedRoute>
            <ProtectedRoute path="/addGarm" reqUser={true}>
              <AddGarm setValue={setValue} />
            </ProtectedRoute>
            <ProtectedRoute path="/myGarms" reqUser={true}>
              <Wardrobe setValue={setValue} />
            </ProtectedRoute>
            <ProtectedRoute path="/fitBuilder" reqUser={true}>
              <OutfitBuilder setValue={setValue} />
            </ProtectedRoute>
            <Route path="*">
              <Redirect to="/login" />
            </Route>
          </Switch>
        </main>
      </Router>
    </ThemeProvider>
  );
}

export default App;
